import React from 'react'
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemText from '@material-ui/core/ListItemText'
import DialogTitle from '@material-ui/core/DialogTitle'
import Dialog from '@material-ui/core/Dialog'
import DialogActions from '@material-ui/core/DialogActions'
import DialogContent from '@material-ui/core/DialogContent'
import DialogContentText from '@material-ui/core/DialogContentText'
import CircularProgress from '@material-ui/core/CircularProgress'
import Button from '@material-ui/core/Button'
import Divider from '@material-ui/core/Divider'
import TextField from '@material-ui/core/TextField'
import Utils from '../Utils'
import Config from '../Config'
import PropTypes from 'prop-types'
import { CopyToClipboard } from 'react-copy-to-clipboard'
import { removeVietnameseTones } from './messenger/utils'
import './ToolsDialog.css'

const copyObj = (obj) => JSON.parse(JSON.stringify(obj))

const TEMPLATES = [
  {
    desc: 'Bo trng bi',
    text: ' c ngi nhp thng tin ca d n / cuc thi / s kin ny. Khng bit bn c mun b sung hay thay i g khng nh?\n\nNu khng, chng mnh s xa thng tin bn  nhp  khng b trng vi bi  ng.'
  },{
    desc: 'Bo b xa v ko ph hp',
    text: 'Xin cho bn,\n\nD n / cuc thi / s kin ca bn  b xa do khng ph hp vi iu khon s dng ca chng mnh.\n\nHy tham kho iu khon s dng ca fanpage ti y: https://sites.google.com/view/tonghopevent'
  }, {
    desc: 'Bo d n c li nhun',
    text: 'Xin cho bn,\n\nD n / cuc thi / s kin ca bn c iu hnh bi mt t chc / c nhn hot ng c li nhun. Da theo iu khon s dng ca chng mnh, bi ca bn phi tr ph 100.000 VN  c th c ng bi ln fanpage nh.\n\nHy tham kho iu khon s dng ca fanpage ti y: https://sites.google.com/view/tonghopevent'
  },
  {
    desc: 'Bo thiu thng tin',
    text: 'Admin  xem bi vit ca bn. Tuy nhin, do bi vit ca bn cn thiu mt s thng tin nn chng mnh cha th ph duyt c. Bn hy in li form vi y  thng tin hn nh!\n\nHy tham kho v d sau: https://bit.ly/2TZKbee'
  },
  {
    desc: 'in li (khng paste link)',
    text: 'Admin  xem bi vit ca bn. Tuy nhin, chng mnh khng th x l nu bn ch copy-paste mi link bi vit. Bn hy in li form tht r rng, bao gm c phn gii thiu,  ngha d n / cuc thi / s kin nh.\n\nHy tham kho v d sau: https://bit.ly/2TZKbee'
  },
]

class ToolsDialog extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      loading: true,
      error: false,
      showTemplates: true,
      sendInboxData: null,
      inboxData: {},
      isLinkCopied: false,
    }
  }

  static propTypes = {
    open: PropTypes.bool.isRequired,
    closeToolsDialog: PropTypes.func.isRequired,
    name: PropTypes.string,
    psid: PropTypes.string,
    doc: PropTypes.any.isRequired,
  }

  async fetchData() {
    this.setState({ error: false, loading: true, showTemplates: true, isLinkCopied: false })
    const response = await Utils.makeRequest(
      `${Config.BACKEND}/inbox/${this.props.psid}`
    )
    const { data } = response
    if (data && Object.keys(data).length > 0) {
      this.setState({
        loading: false,
        error: false,
        inboxData: data,
      })
    } else {
      this.setState({
        loading: false,
        error: true,
      })
    }
  }

  async sendInbox() {
    const inboxData = copyObj(this.state.sendInboxData)
    this.setState({sendInboxData: null})
    this.props.closeToolsDialog()
    const res = await Utils.makeRequest(
      `${Config.BACKEND}/inbox/${this.props.psid}`,
      'post', { message: ' ' + inboxData.text }
    )
    if (res.data && res.data.success) {
      window.toastr.success(' gi tin nhn thnh cng!')
    } else {
      window.toastr.error(' C li xy ra, ko th gi tin nhn ')
    }
  }

  goToPageInbox() {
    Utils.goToInbox(this.state.inboxData);
    this.props.closeToolsDialog();
  }

  _renderSendInboxDialogAsk() {
    const { sendInboxData } = this.state;
    const isWithin24HWindow = (Date.now() - this.props.doc.created) < (86400 * 1000)
    const handleClose = () => this.setState({sendInboxData: null});
    if (!sendInboxData) return null;
    else return <Dialog open={!!sendInboxData} onClose={handleClose}>
      <DialogTitle id="alert-dialog-title">{sendInboxData.desc}</DialogTitle>
      <DialogContent>
        <DialogContentText id="alert-dialog-description">
          {
            isWithin24HWindow
             ? 'Gi tin nhn c ni dung nh sau (c th sa):'
             : 'Khng th t ng gi tin nhn. Hy copy-paste v inbox th cng:'
          }<br/><br/>
          <TextField
            multiline
            rowsMax="20"
            value={sendInboxData.text}
            onChange={event => {this.setState({
              sendInboxData: {
                ...sendInboxData,
                text: event.target.value,
              }
            })}}
            margin="normal"
            variant="outlined"
            fullWidth
          />
        </DialogContentText>
      </DialogContent>
      <DialogActions>
        <Button onClick={handleClose} color="primary"> Quay li </Button>
        {!isWithin24HWindow
          && <Button onClick={() => Utils.copyTextToClipboard(sendInboxData.text)} color="primary"> Copy tin nhn </Button>
        }
        {isWithin24HWindow
          ? <Button onClick={() => this.sendInbox()} color="primary"> GI </Button>
          : <Button onClick={() => this.goToPageInbox()} color="primary"> i ti inbox </Button>
        }
      </DialogActions>
    </Dialog>
  }

  _renderError() {
    return (
      <List>
        <ListItem button>
          <ListItemText primary="(C li xy ra)" />
        </ListItem>
        <ListItem button onClick={this.props.closeToolsDialog}>
          <ListItemText primary="Quay li" />
        </ListItem>
      </List>
    )
  }

  _renderContent() {
    const { inboxData, showTemplates, isLinkCopied } = this.state

    return (
      <List>
        <ListItem button onClick={this.goToPageInbox.bind(this)}>
          <ListItemText
            primary="i ti inbox"
            secondary={`Tn FB: ${inboxData.senders.data[0].name}`}
          />
        </ListItem>
        <Divider />
        {!showTemplates && <ListItem button onClick={() => this.setState({showTemplates: true})}>
          <ListItemText primary={` (Hin cc mu tin nhn)`} />
        </ListItem>}
        {showTemplates && TEMPLATES.map((t, i) => {
          return <ListItem button onClick={() => this.setState({ sendInboxData: t })} key={i}>
            <ListItemText primary={` ${t.desc}`} />
          </ListItem>
        })}
        <Divider />
          <CopyToClipboard
            text={`${window.location.protocol}//${window.location.host}/data/#/view/${this.props.doc.id}`}
            onCopy={() => this.setState({isLinkCopied: true})}
          >
            <ListItem button>
              <ListItemText primary={isLinkCopied ? "( copy link)" : "Copy link bi vit"} />
            </ListItem>
          </CopyToClipboard>
        <Divider />
        <ListItem button onClick={this.props.closeToolsDialog}>
          <ListItemText primary="Quay li" />
        </ListItem>
      </List>
    )
  }

  render() {
    const { name, open } = this.props

    return (
      <React.Fragment>
        {this._renderSendInboxDialogAsk()}
        <Dialog
          open={open}
          onEnter={this.fetchData.bind(this)}
          onBackdropClick={this.props.closeToolsDialog}
          className="tools-dialog"
          aria-labelledby="simple-dialog-title"
        >
          <DialogTitle id="simple-dialog-title">{name ? name.substr(0,24) : ''}</DialogTitle>
          {this.state.loading
            ? <center><CircularProgress /></center>
            : this.state.error ? this._renderError() : this._renderContent()
          }
        </Dialog>
      </React.Fragment>
    )
  }
}

export default ToolsDialog
