import React from 'react'
import CircularProgress from '@material-ui/core/CircularProgress'
import axios from 'axios'
import Utils from '../Utils'
import Card from '@material-ui/core/Card'
import CardContent from '@material-ui/core/CardContent'
import TextField from '@material-ui/core/TextField'
import Button from '@material-ui/core/Button'
import Img from 'react-image'

const imgStyle = {
  maxHeight: '300px',
  maxWidth: '100%',
}

const imgLoaderStyle = {
  height: '300px',
  width: '100%',
}

const classes = {
  cardStyle: {
    maxWidth: '800px',
    marginTop: '20px',
    marginRight: 'auto',
    marginLeft: 'auto',
  }
}

class GetImage extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      showGetToken: false,
      access_token: '',
      completed: false,
      loading: true,
      imgs: [],
    }
    if (props.data) {
      this.state.caption = props.data.caption
      this.state.fbusername = props.data.fbusername
    } else {
      this.state.error = 'Hy v trang ch v th li'
    }
    window.changeHeader('Chn nh bi ng - TongHopEvent')
  }

  componentDidMount() {
    this.fetchFacebookApi()
  }

  async fetchFacebookApi() {
    const fbtoken = Utils.getLocalStorage('fbtoken')
    try {
      const res = await axios.get(`https://graph.facebook.com/${this.state.fbusername}?fields=feed.limit(20)%7Bfull_picture%7D%2Cpicture.width(720)%7Burl%7D&access_token=${fbtoken}`)
      if (res.data.error) {
        this.setState({loading: false, showGetToken: true})
      } else {
        const imgs = res.data.feed.data.map(post => post.full_picture)
        imgs.unshift(res.data.picture.data.url)
        this.setState({
          loading: false,
          showGetToken: false,
          imgs: imgs,
        })
      }
    } catch (e) {
      if (e.response.status === 404) {
        this.setState({loading: false, error: 'Khng th m trang facebook ny'})
      } else {
        this.setState({loading: false, showGetToken: true})
      }
    }
  }

  onSaveToken() {
    let fbtoken = this.state.access_token
    fbtoken = fbtoken.match(/EA[a-zA-Z0-9]+/)
    try {
      Utils.setLocalStorage('fbtoken', fbtoken[0])
      const urlNow = window.location.hash
      window.location.hash = '#/reload'
      setTimeout(() => { window.location.hash = urlNow }, 50)
    } catch (e) {
      window.alert('token khng hp l')
    }
  }

  _renderTitleCard() {
    return (
      <Card>
        <CardContent>
          <p>
            <Button
              variant='outlined'
              size='small'
              onClick={() => window.location.hash = '#/'}>Tr v</Button>
            &nbsp;&nbsp;&nbsp;
            <b>Chn nh cho bi ng</b>
          </p>
        </CardContent>        
      </Card>
    )
  }

  _renderImageCard(url) {
    return (
      <Card>
        <CardContent>
          <center>
            <Img alt=''
              src={url}
              style={imgStyle}
              loader={<div style={imgLoaderStyle}><center><br/><br/><CircularProgress /></center></div>}
            />  
          </center>
          <Button
              variant='outlined'
              size='small'
              style={{ float: 'right', margin: '10px' }}
              onClick={() => {
                const _data = JSON.parse(JSON.stringify(this.props.data))
                _data.url = url
                this.props.gotoPublish(_data, 'publish')
              }}>Chn nh ny</Button>
        </CardContent>
      </Card>
    )
  }

  _renderGetTokenCard() {
    return (
      <Card>
        <CardContent>
          <p>
            <Button
              variant='outlined'
              size='small'
              onClick={() => window.location.hash = '#/'}
            >Tr v</Button>
            &nbsp;&nbsp;&nbsp;
            <b>Ly token</b><br/>
            <br/>
            Bn cha c token hoc token  b ht hn. Hy lm theo hng dn ny  ly token: <a href="https://blog.ngxson.com/tonghopevent/#lay-token" target="_blank" rel="noopener noreferrer">[Bm vo y]</a><br/>
            Lu : token s c lu trn my ny. Bn c th ly token t my tnh  dng trn T.<br/>
            in token ca bn vo  di y:<br/>
          </p>
          <TextField
            multiline
            rowsMax="5"
            value={this.state.access_token}
            onChange={event => {this.setState({ access_token: event.target.value });}}
            margin="normal"
            variant="outlined"
            fullWidth
          /><br/>
          <Button
            variant='outlined'
            onClick={this.onSaveToken.bind(this)}
          >Tip tc</Button>
        </CardContent>        
      </Card>
    )
  }

  _renderMain() {
    return (
      <React.Fragment>
        {this.state.showGetToken &&
          <div style={classes.cardStyle}>
            {this._renderGetTokenCard()}
          </div>
        }
        {!this.state.showGetToken &&
          <div style={classes.cardStyle}>
            {this._renderTitleCard()}
          </div>
        }
        {!this.state.showGetToken &&
          this.state.imgs.map((url, i) =>
            <div style={classes.cardStyle} key={i}>
              {this._renderImageCard(url)}
            </div>
          )
        }
        <br />
        <br />
      </React.Fragment>
    )
  }

  _renderError() {
    return (
      <center>
        <br/><br/>
        <h2>C li xy ra</h2>
        {this.state.error}<br/><br/>
        <Button
          variant='outlined'
          size='small'
          onClick={() => window.location.hash = '#/'}
        >Tr v</Button>
        <br/><br/>
      </center>
    )
  }

  render() {
    const loading = <div>
      <br/><br/><br/>
      <center><CircularProgress /></center>
    </div>

    if (this.state.loading) return loading
    if (this.state.error) return this._renderError()

    return this._renderMain()
  }
}

export default GetImage