import React from 'react';

const Data = [

  // INDEX 0
  (parent) => {
    return {
      type: 'small',
      title: 'Bn mun ng bi v',
      options: [
        { text: 'D n', subtitle: 'Hot ng ngoi kha', value: 'D n' },
        { text: 'Cuc thi', subtitle: 'Dnh cho HS, SV', value: 'Cuc thi' },
        { text: 'S kin', subtitle: 'Dnh cho HS, SV' , value: 'S kin' },
      ],
      notice: 'Lu : Fanpage khng nhn ng bi v tuyn dng vic lm.',
      next: (value) => {
        const newState = {
          screen: 1,
          type: value,
        };
        if (value === 'Cuc thi') newState.tag = '#cuc_thi';
        else if (value === 'S kin') newState.tag = '#s_kin';
        parent.setState(newState);
      }
    };
  },

  // INDEX 1
  (parent) => {
    return {
      type: 'long',
      title: <React.Fragment>{parent.state.type} ca bn c <b>iu hnh bi</b></React.Fragment>,
      options: [
        { text: 'Mt nhm hc sinh, sinh vin', subtitle: 'Hot ng phi li nhun', value: 'F1' },
        { text: 'Mt cu lc b trong trng hc', subtitle: 'Hot ng phi li nhun', value: 'F2' },
        { text: 'Mt trng hc cng lp', subtitle: 'VD: trng THCS, THPT, H, Cao ng,...', value: 'F3' },
        { text: 'Mt t chc phi li nhun', subtitle: 'VD: AIESEC, VOGE, SOTB,...', value: 'F4' },
        { text: 'T chc hot ng c li nhun', subtitle: 'VD: trung tm ngoi ng, ca hng,...', value: 'P1' },
      ],
      next: (value) => {
        if (value === 'P1') {
          parent.setState({ screen: 2 });
        } else {
          window.location.replace(parent.getFormUrl(value));
        }
      }
    };
  },

  // INDEX 2
  (parent) => {
    return {
      type: 'basic',
      title: <React.Fragment>
        Da theo <a href="https://sites.google.com/view/tonghopevent" target="_blank" rel="noopener noreferrer">iu khon s dng</a> ca chng mnh, {parent.state.type.toLowerCase()} c t chc hoc c iu hnh bi mt hoc nhiu doanh nghip, t chc hay c nhn <b>hot ng c li nhun</b> phi tr ph theo <b>Gi P1</b><br/>
        <br/>
        Gi P1 c gi l 100.000 VN / 1 bi ng [<a href="https://sites.google.com/view/tonghopevent/gi-p1" target="_blank" rel="noopener noreferrer">Bm y  xem chi tit</a>]<br/>
        <br/>
         tip tc, bn hy nhp thng tin vo form sau:
        &nbsp;
        <button className="btn btn-small btn-primary"
          style={{padding: '4px'}}
          onClick={() => {
            window.location.replace(parent.getFormUrl('P1'));
          }}
        >Ti Form </button><br/>
        Sau , hng dn thanh ton s c gi ti inbox ca bn.
      </React.Fragment>
    };
  },
];

export default Data;